.HeaderC {
  background: linear-gradient(45deg, var(--color-purple), var(--color-yellow));
  color: var(--color-cloud);
  height: 75vh;
  display: flex;
  align-items: center;
  align-content: space-between;
  @include from(large) {
    height: 70vh;
  }
  &__header {
    display: flex;
    flex-direction: column-reverse;
    @include from(large) {
      flex-direction: initial;
      align-items: center;
    }
  }

  &__title {
    text-transform: initial;
    @include from(large) {
      width: 50%;
      margin-bottom: 0;
    }
  }
  &__image {
    width: 250px;
    max-width: 450px;
    margin: 0 auto;
    @include from(large) {
      width: 400px;
    }
  }
}
///////////////////////////////////////////////////////////
.Contact {
  padding: 1.5rem 0;
  &__title {
    color: var(--color-accent);
  }
  &__form {
    width: 100%;
    text-align: center;
  }
  &__input,
  &__text {
    border: 1px solid var(--color-aqua);
    width: 100%;
    border-radius: 50px;
    background-color: transparent;
    box-shadow: none;
    padding: 1.2rem;
  }
  &__inputs {
    width: 100%;
    @include from(large) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__input {
    height: 2rem;
    margin-bottom: 1rem;
    @include from(large) {
      width: 300px;
    }
    @include from(xlarge) {
      width: 350px;
    }
    @include from(xxlarge) {
      width: 400px;
    }
  }
  &__text {
    resize: none;
    height: 12rem;
    border-radius: 15px;
  }
  &__btn {
    padding: 0.6rem 1.3rem;
    border-radius: 50px;
    width: 8rem;
    color: var(--color-aqua);
    border: 1px solid var(--color-aqua);
    margin: 0.7rem 0;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: var(--color-aqua);
      color: var(--color-cloud);
    }
  }
  &__details {
    color: var(--color-cloud);
    background: var(--color-dark);
    padding: 2rem 3rem;
    border-radius: 7px 50px 7px 7px;
    width: 100%;
    text-align: center;
  }
  &__channels {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  &__channelsTitle {
    width: 100%;
    text-transform: uppercase;
    font-weight: 600;
    color: var(--color-accent);
  }
  &__mails,
  &__numbers {
    text-align: left;
  }
  &__hoursTitle {
    text-transform: uppercase;
    font-weight: 600;
    color: var(--color-accent);
  }
  &__maps {
    width: 100%;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    @include from(large) {
      justify-content: space-between;
    }
  }
  &__mapsTitle {
    text-transform: uppercase;
    font-weight: 600;
    color: var(--color-accent);
    width: 100%;
  }
  &__map {
    width: 100%;
    @include from(large) {
      width: 48%;
    }
  }
  & iframe {
    width: 100%;
    height: 400px;
    border: 1px solid vaR(--color-aqua);
    border-radius: 5px;
    box-shadow: 1px 1px 3px var(--color-aqua);
  }
}

textarea:focus,
input:focus,
button:focus {
  outline: none;
}
