.HeaderA {
  background: linear-gradient(45deg, var(--color-purple), var(--color-aqua));
  color: var(--color-cloud);
  height: 75vh;
  display: flex;
  align-items: center;
  align-content: space-between;
  @include from(large) {
    height: 70vh;
  }
  &__header {
    display: flex;
    flex-direction: column-reverse;
    @include from(large) {
      flex-direction: initial;
      align-items: center;
    }
  }
  &__content {
    @include from(large) {
      width: 50%;
    }
  }
  &__title {
    @include from(large) {
      margin-bottom: 0;
    }
  }
  &__image {
    width: 250px;
    max-width: 450px;
    margin: 0 auto;
    @include from(large) {
      width: 400px;
    }
  }
}

////////////////////////////////////////////////////////////////////////////
.About {
  &__content {
  }
  &__items {
    margin: 0 auto;
    @include from(large) {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  }
  &__mision {
    color: var(--color-purple);
    text-transform: uppercase;
    font-weight: 500;
    @include from(large) {
      width: 40%;
    }
  }
  & img {
    margin: 0 auto;
    padding: 1rem 0;
    min-width: 300px;
  }
}
