$max-width: 1640px;
$header-height: 5rem;

$breakpoints: (
  small: 0px,
  medium: 640px,
  large: 1024px,
  xlarge: 1200px,
  xxlarge: 1440px,
);
// Colors
:root {
  --color-cloud: #fafafa;
  --color-dark: #353535;
  --color-accent: #d90467;
  --color-hover: #ff4085;
  --color-blue: #30a8f2;
  --color-yellow: #fbd53a;
  --color-laqua: #05f2db;
  --color-aqua: #00cde2;
  --color-purple: #6c0fd8;
}

// z-index
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

// tipograf√≠a
:root {
  --h1-font-size: 2rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --body-font-size: 0.9rem;
  --small-font-size: 0.7rem;
  @include from(large) {
    --h1-font-size: 2.5rem;
    --h2-font-size: 1.7rem;
    --h3-font-size: 1.5rem;
    --body-font-size: 1rem;
    --small-font-size: 0.8rem;
  }
  @include from(xlarge) {
    --h1-font-size: 3.3rem;
  }
}
