.Footer {
  background: linear-gradient(var(--color-aqua), var(--color-blue));
  color: var(--color-cloud);
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 0.2rem;
  &__logo {
    width: 55px;
    margin: 0 auto;
  }
  &__title {
    width: 100%;
    font-weight: 600;
    margin-top: 0.2rem;
  }
  &__items {
    margin: 0 auto;
    width: 100%;
    @include from(large) {
      display: flex;
      justify-content: space-between;
      text-align: left;
      margin: 1.5rem 0 1.1rem;
    }
  }
  &__address,
  &__cellphone,
  &__mail,
  &__social {
    margin-bottom: 1rem;
    @include from(large) {
      margin-bottom: 0;
    }
  }
  &__address,
  &__cellphone,
  &__mail {
    border-bottom: 1px solid rgba(251, 251, 251, 0.3);
    @include from(large) {
      border-bottom: 0;
    }
  }
  &__address {
    border-top: 1px solid rgba(251, 251, 251, 0.3);
    @include from(large) {
      border-top: 0;
    }
  }
  &__item {
    font-size: 0.8rem;
    @include from(xlarge) {
      font-size: 0.9rem;
    }
  }
  &__copy {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 0.7rem;
    @include from(large) {
      font-size: 0.8rem;
    }
  }
}
