.HeaderPr {
  background: linear-gradient(45deg, var(--color-purple), var(--color-accent));
  color: var(--color-cloud);
  height: 75vh;
  display: flex;
  align-items: center;
  align-content: space-between;
  @include from(large) {
    height: 70vh;
  }
  &__header {
    display: flex;
    flex-direction: column-reverse;
    @include from(large) {
      flex-direction: initial;
      align-items: center;
    }
  }
  &__title {
    margin-top: 1rem;
    text-transform: initial;
    @include from(large) {
      width: 50%;
      margin-top: 0;
    }
  }
  &__image {
    width: 250px;
    max-width: 450px;
    margin: 0 auto;
    @include from(large) {
      width: 400px;
    }
  }
}
//////////////////////////////////////////////////////////////////
.Portfolio {
  padding: 1rem 0;
  &__customers {
    display: grid;
    grid-gap: 1rem;
    align-items: center;
    margin: 0 auto;
    grid-auto-flow: dense;
    @include from(medium) {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(10, auto);
    }
    @include from(large) {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(7, auto);
    }
  }
  &__items {
    display: grid;
    justify-items: stretch;
    align-items: stretch;
    @include from(medium) {
      height: 300px;
    }
    &:nth-child(3) {
      @include from(medium) {
        height: 616px;
        grid-area: 1/2 / 3/2;
      }
    }
    &:nth-child(5) {
      @include from(medium) {
        height: 616px;
        grid-area: 3/1 / 5/1;
      }
    }
    &:nth-child(6) {
      @include from(large) {
        grid-area: 6/1 / span 2 / span 2;
        height: 616px;
      }
    }
  }
  & img {
    border-radius: 0.2rem;
    box-shadow: 1px 1px 3px var(--color-dark);
    min-height: 300px;
    object-fit: cover;
  }
}
