.Social {
  width: 192px;
  max-width: 192px;
  margin: 0 auto;
  @include from(medium) {
    width: 50%;
  }
  @include from(large) {
    margin: 0;
    width: 40%;
  }
  &__items {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    @include from(large) {
      justify-content: space-between;
    }
  }
  &__link {
    width: 30px;
    height: 30px;
    background: var(--color-cloud);
    border-radius: 50%;
    box-shadow: 0 1px 3px var(--color-cloud);
    display: flex;
    align-items: center;
    justify-content: center;
    @include from(800px) {
      width: 35px;
      height: 35px;
    }
  }
  &__icons {
    padding: 0.3rem;
  }
}
