.l-container {
  max-width: $max-width;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  flex-wrap: wrap;
  margin-right: auto;
  margin-left: auto;
}

////////////////////////////
@for $i from 1 through 20 {
  .small-#{$i*5} {
    width: #{$i * 5%};
  }
}

@include from(large) {
  @for $i from 1 through 20 {
    .large-#{$i*5} {
      width: #{$i * 5%};
    }
  }
}

.center-block {
  margin-left: auto;
  margin-right: auto;
}

.center-content {
  text-align: center;
}
