.Landing {
  background: url(../static/bg.webp);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-cloud);
  flex-direction: column;
  &__items {
    flex-direction: column-reverse;
    margin-bottom: 2.2rem;
    @include from(large) {
      flex-direction: initial;
      flex-wrap: nowrap;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 3rem;
    }
  }
  &__content {
    text-align: center;
    @include from(large) {
      text-align: initial;
    }
    @include from(large) {
      width: 50%;
    }
  }
  &__title {
    margin-top: 2rem;
  }
  &__description {
    width: 80%;
    margin: 0 auto;
    @include from(large) {
      width: 100%;
    }
  }
  &__cta {
    background: var(--color-accent);
    color: var(--color-cloud);
    padding: 0.6rem 1.3rem;
    border-radius: 50px;
    font-weight: 500;
    box-shadow: 1px 1px 3px var(--color-accent);
    display: inline-block;
    margin-top: 2.2rem;
    @include from(large) {
      padding: 0.8rem 2rem;
    }
  }
  &__banner {
    max-width: 820px;
    @include from(large) {
      width: 50%;
    }
  }
}
//////////////////////////main content//////////////////////////////////////

.Main {
  &__content {
    text-align: center;
    margin-top: 2rem;
    color: var(--color-dark);
  }
  &__title,
  &__subTitle {
    color: var(--color-accent);
    margin-bottom: 0;
  }
  &__stand {
    @include from(800px) {
      margin: -12rem auto 7rem;
      width: 30%;
      text-align: center;
    }
    @include from(xxlarge) {
      margin: -15rem auto 9rem;
    }
  }
  &__divisor {
    margin: -0.2rem auto 0;
    @include from(800px) {
      margin: -0.2rem auto 2rem;
    }
  }
  &__start {
    background: var(--color-dark);
    width: 100%;
    border-radius: 7px 50px 7px 7px;
    text-align: center;
    padding: 2rem 3rem;
    color: var(--color-cloud);
  }
  &__startTitle {
    font-size: 2rem;
    font-weight: 500;
    margin: 0;
  }
  &__startButtons {
    margin-top: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include from(768px) {
      flex-direction: initial;
      justify-content: center;
    }
  }
  &__button {
    color: var(--color-cloud);
    background: var(--color-accent);
    padding: 0.8rem 1rem;
    border-radius: 50px;
    transition: all 0.2s;
    width: 210px;
    height: 45px;
    &:first-child {
      margin-bottom: 1.5rem;
      @include from(768px) {
        margin: 0 2rem 0 0;
      }
    }
    &:last-child {
      background: none;
      border: 1px solid var(--color-accent);
      color: var(--color-accent);
    }
    &:last-child:hover {
      background: var(--color-accent);
      color: var(--color-cloud);
    }
    &:first-child:hover {
      background: var(--color-hover);
    }
  }
  &__delivery {
    margin-top: -2.3rem;
    @include from(large) {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      margin: 0 auto;
    }
  }
  &__deliveryDescription {
    font-size: 1.8rem;
    text-transform: uppercase;
    color: var(--color-accent);
    font-weight: 600;
    margin: -2.7rem 0 1rem;
    @include from(large) {
      margin: 0;
      font-size: 2.1rem;
    }
  }
}
