.main-header {
  height: $header-height;
  position: fixed;
  width: 100%;
  z-index: var(--z-fixed);
  &__block {
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
  transition: all 0.6s;
}
.main-logo {
  width: 3rem;
}

.sticky {
  background: var(--color-aqua);
}
