.HeaderS {
  background: linear-gradient(45deg, var(--color-accent), var(--color-yellow));
  color: var(--color-cloud);
  height: 75vh;
  display: flex;
  align-items: center;
  align-content: space-between;
  @include from(large) {
    height: 70vh;
  }
  &__header {
    display: flex;
    flex-direction: column-reverse;
    @include from(large) {
      flex-direction: initial;
      align-items: center;
    }
  }
  &__title {
    text-transform: initial;
    @include from(large) {
      width: 50%;
      margin-bottom: 0;
    }
  }
  &__image {
    width: 300px;
    max-width: 500px;
    margin: 0 auto;
    @include from(large) {
      width: 450px;
    }
  }
}
.Services {
  text-align: center;
  &__engagement {
    font-weight: 500;
  }
  & h3 {
    color: var(--color-accent);
    @include from(large) {
      font-size: 1.7rem;
      margin: 0 auto;
    }
  }
  &__banner,
  &__laser,
  &__print {
    margin-top: 1rem;
    @include from(large) {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__bannerItems,
  &__laserItems,
  &__printItems {
    border-radius: 5px;
    padding: 1rem;
    @include from(large) {
      width: 70%;
    }
  }
  &__bannerItems {
    background: linear-gradient(45deg, var(--color-aqua), var(--color-blue));
  }
  &__laserItems {
    background: linear-gradient(
      45deg,
      var(--color-accent),
      var(--color-yellow)
    );
    @include from(large) {
      order: -1;
    }
  }
  &__printItems {
    background: linear-gradient(45deg, var(--color-purple), var(--color-laqua));
  }
  &__secondary {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 1rem 0;
  }
  &__design,
  &__plotter,
  &__stamp,
  &__copy {
    width: 49%;
    padding: 0.3rem;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    @include from(large) {
      width: 24%;
    }
  }
  &__stamp,
  &__copy {
    margin-top: 1rem;
    @include from(large) {
      margin-top: 0;
    }
  }
  &__design {
    background: linear-gradient(var(--color-cloud) 25%, var(--color-hover));
    border: solid 1px var(--color-hover);
    & h3 {
      color: var(--color-hover);
    }
  }
  &__plotter {
    background: linear-gradient(var(--color-cloud) 25%, var(--color-aqua));
    border: solid 1px var(--color-aqua);
    & h3 {
      color: var(--color-aqua);
    }
  }
  &__stamp {
    background: linear-gradient(var(--color-cloud) 25%, var(--color-purple));
    border: solid 1px var(--color-purple);
    & h3 {
      color: var(--color-purple);
    }
  }
  &__copy {
    background: linear-gradient(var(--color-cloud) 25%, var(--color-laqua));
    border: solid 1px var(--color-laqua);
    & h3 {
      color: var(--color-laqua);
    }
  }
  & img {
    height: auto;
  }
}
