.main-menu {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  height: 100%;
  text-align: center;
  @include from(large) {
    display: flex;
  }
  &__item {
    padding: 1rem;
    &:last-child {
      padding-right: 0;
    }
  }
  &__link {
    color: var(--color-cloud);
    text-transform: uppercase;
    display: block;
    padding: 1.5rem;
    transition: all 0.3s;
    &:hover {
      border-bottom: 1px solid var(--color-cloud);
    }
    @include from(large) {
      padding: 0;
      margin: 0;
    }
  }
}
.main-nav {
  position: fixed;
  height: calc(100vh - #{$header-height});
  width: 80vw;
  background: linear-gradient(var(--color-purple), var(--color-aqua));
  top: $header-height;
  right: -80vw;
  transition: right 0.3s;
  z-index: var(--z-fixed);

  @include from(large) {
    position: static;
    background: transparent;
    padding-top: 0;
    height: auto;
    width: auto;
  }

  &.show {
    right: 0;
  }
}
.main-menu__toggle {
  width: 2.4rem;
  height: 1.9rem;
  background: linear-gradient(
    var(--color-cloud) 20%,
    transparent 20%,
    transparent 40%,
    var(--color-cloud) 40%,
    var(--color-cloud) 60%,
    transparent 60%,
    transparent 80%,
    var(--color-cloud) 80%
  );
  cursor: pointer;
  @include from(large) {
    display: none;
  }
}
