.HeaderP {
  background: linear-gradient(45deg, var(--color-blue), var(--color-aqua));
  color: var(--color-cloud);
  height: 75vh;
  display: flex;
  align-items: center;
  align-content: space-between;
  @include from(large) {
    height: 70vh;
  }
  &__header {
    display: flex;
    flex-direction: column-reverse;
    @include from(large) {
      flex-direction: initial;
      align-items: center;
    }
  }
  &__title {
    text-transform: initial;
    @include from(large) {
      width: 50%;
      margin-bottom: 0;
    }
  }
  &__image {
    width: 250px;
    max-width: 450px;
    margin: 0 auto;
    @include from(large) {
      width: 400px;
    }
  }
}

.Products {
  // background: var(--color-purple);
  &__shop {
    width: 100%;
    padding: 1rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;

    @include from(900px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @include from(xlarge) {
      grid-template-columns: repeat(4, 1fr);
    }
    @include from(xxlarge) {
      grid-template-columns: repeat(5, 1fr);
    }
  }
  &__items {
    border: 1px solid var(--color-purple);
    border-radius: 5px;
    background: linear-gradient(var(--color-cloud), var(--color-purple));
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-width: 180px;
  }
  & img {
    height: 200px;
  }
  & p {
    color: var(--color-cloud);
    font-weight: 500;
  }
}
